'use strict';

/**
* Flatten a three-dimensional nested array.
*
* @module @stdlib/array-base-flatten3d
*
* @example
* import flatten3d from '@stdlib/array-base-flatten3d';
*
* var x = [ [ [ 1, 2 ] ], [ [ 3, 4 ] ] ];
*
* var out = flatten3d( x, [ 2, 1, 2 ], false );
* // returns [ 1, 2, 3, 4 ]
*
* @example
* import flatten3d from '@stdlib/array-base-flatten3d';
*
* var x = [ [ [ 1, 2 ] ], [ [ 3, 4 ] ] ];
*
* var out = flatten3d( x, [ 2, 1, 2 ], true );
* // returns [ 1, 3, 2, 4 ]
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import flatten3d from '@stdlib/array-base-flatten3d';
*
* var x = [ [ [ 1, 2 ] ], [ [ 3, 4 ] ] ];
*
* var out = new Float64Array( 4 );
* var y = flatten3d.assign( x, [ 2, 1, 2 ], true, out, 1, 0 );
* // returns <Float64Array>[ 1, 3, 2, 4 ]
*
* var bool = ( y === out );
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import assign from './assign.js';


// MAIN //

setReadOnly( main, 'assign', assign );


// EXPORTS //

export default main;
